"use strict";
// restored state placeholder
let restoredModel;
let restoredUI;
const vscode = acquireVsCodeApi();
// try restore
try {
    const saved = vscode.getState && vscode.getState();
    if (saved && saved.model) {
        restoredModel = saved.model;
    }
    if (saved && saved.ui) {
        restoredUI = saved.ui;
    }
}
catch { }
let model = {
    colors: {},
    tokenColors: [],
    semanticTokens: {},
};
// Debounce state for preview updates
let previewTimer;
const PREVIEW_DEBOUNCE_MS = 120;
let lastPreviewSnapshot;
const historyStack = [];
const redoStack = [];
let lastHistoryTs = 0;
const HISTORY_MIN_INTERVAL_MS = 500;
let baselineSnapshot;
const deepClone = (x) => JSON.parse(JSON.stringify(x));
const recordHistory = () => {
    const now = Date.now();
    if (now - lastHistoryTs < HISTORY_MIN_INTERVAL_MS)
        return;
    historyStack.push(deepClone(model));
    // cap history to 50 entries
    if (historyStack.length > 50)
        historyStack.shift();
    // new edits invalidate redo
    redoStack.length = 0;
    lastHistoryTs = now;
    updateUndoRedoButtons();
};
const undo = () => {
    if (!historyStack.length)
        return;
    redoStack.push(deepClone(model));
    const prev = historyStack.pop();
    model = deepClone(prev);
    renderAll();
    pushPreview();
    updateUndoRedoButtons();
};
const redo = () => {
    if (!redoStack.length)
        return;
    historyStack.push(deepClone(model));
    const next = redoStack.pop();
    model = deepClone(next);
    renderAll();
    pushPreview();
    updateUndoRedoButtons();
};
function updateUndoRedoButtons() {
    const undoBtn = document.getElementById("btn-undo");
    const redoBtn = document.getElementById("btn-redo");
    if (undoBtn)
        undoBtn.disabled = historyStack.length === 0;
    if (redoBtn)
        redoBtn.disabled = redoStack.length === 0;
    const status = document.getElementById('status');
    if (status) {
        const dirty = JSON.stringify(model) !== (baselineSnapshot || '');
        status.textContent = `${dirty ? '● ' : ''}Undo:${historyStack.length} Redo:${redoStack.length}`;
    }
}
function resetHistory() {
    historyStack.length = 0;
    redoStack.length = 0;
    lastHistoryTs = 0;
    baselineSnapshot = JSON.stringify(model);
    updateUndoRedoButtons();
}
let categories = [];
window.addEventListener("message", (e) => {
    const { type, payload } = e.data;
    if (type === "BOOT") {
        categories = payload.categories || [];
        const current = payload.settings || {};
        // If user wants to start from current
        if (restoredModel) {
            model = deepClone(restoredModel);
        }
        else {
            model.colors = current.colors || {};
            model.tokenColors = current.tokenColors?.textMateRules || [];
            model.semanticTokens = current.semanticTokens?.rules || {};
        }
        renderAll();
        resetHistory();
        // restore UI state
        if (restoredUI) {
            const left = restoredUI.leftTab || 'colors';
            document.querySelectorAll('.tab').forEach((t) => t.classList.remove('active'));
            document.querySelector(`[data-tab="${left}"]`)?.classList.add('active');
            document.querySelectorAll('.panel').forEach((x) => x.classList.remove('active'));
            document.getElementById('panel-' + left)?.classList.add('active');
            const p = restoredUI.previewTab || 'editor';
            document.querySelectorAll('.ptab').forEach((t) => t.classList.remove('active'));
            document.querySelector(`[data-demo="${p}"]`)?.classList.add('active');
            document.querySelectorAll('.demo').forEach((x) => (x.style.display = 'none'));
            document.getElementById('demo-' + p).style.display = 'block';
            if (typeof restoredUI.search === 'string') {
                const search = document.getElementById('search');
                search.value = restoredUI.search;
                const event = new Event('input');
                search.dispatchEvent(event);
            }
        }
    }
    if (type === "LOAD_CURRENT") {
        model.colors = mergeColors(model.colors, payload.colors || {});
        model.tokenColors = mergeTokenRules(model.tokenColors, payload.tokenColors || []);
        model.semanticTokens = mergeSemanticRules(model.semanticTokens, payload.semanticTokens || {});
        renderAll();
        resetHistory();
        pushPreview();
    }
    if (type === "LOAD_IMPORTED") {
        model.colors = payload.colors || {};
        model.tokenColors = payload.tokenColors || [];
        model.semanticTokens = payload.semanticTokens || {};
        renderAll();
        resetHistory();
        pushPreview();
    }
    if (type === "LOCATE")
        pulse(payload?.elementId);
    if (type === "UI_UNDO")
        undo();
    if (type === "UI_REDO")
        redo();
});
function pulse(id) {
    if (!id)
        return;
    const el = document.getElementById(id);
    if (!el)
        return;
    el.classList.add("locate-pulse");
    setTimeout(() => el.classList.remove("locate-pulse"), 1000);
}
function renderAll() {
    renderColors();
    renderTokens();
    renderSemantic();
    renderPreviewDemos();
}
function inputRow(label, key, description) {
    const id = `color-${key}`;
    const v = model.colors[key] || "";
    const alpha = alphaFromHex(v);
    return `
    <div class="row">
      <div class="row-head">
        <strong>${label}</strong>
        <button data-locate="${key}" title="Locate in Preview" aria-label="Locate ${key} in preview">Locate</button>
      </div>
      <div class="row-body">
        <input type="color" data-key="${key}" value="${coerceHex(v)}" aria-label="Color value for ${key}" />
		<input type="text" data-key="${key}" value="${v}" placeholder="#RRGGBB or #RRGGBBAA" aria-label="Hex color for ${key}" />
		<input type="range" min="0" max="100" step="1" data-alpha-key="${key}" value="${alpha}" title="Alpha ${alpha}%" aria-label="Alpha for ${key}" />
        <span class="desc">${description || ""}</span>
      </div>
    </div>`;
}
function coerceHex(s) {
    // fallback to #000000 if invalid
    return /^#([0-9a-f]{6}|[0-9a-f]{8})$/i.test(s)
        ? `#${s.replace("#", "").slice(0, 6)}`
        : "#000000";
}
function isValidHex(s) {
    return /^#[0-9a-f]{6}([0-9a-f]{2})?$/i.test(s.trim());
}
function normalizeHex(s) {
    // Uppercase and ensure leading '#'
    let v = s.trim();
    if (!v.startsWith("#"))
        v = "#" + v;
    return v.toUpperCase();
}
function renderColors() {
    const root = document.getElementById("panel-colors");
    root.innerHTML = categories
        .map((c) => `
    <section>
      <h4>${c.name}</h4>
      ${c.items
        .map((it) => inputRow(it.key, it.key, it.description || ""))
        .join("")}
    </section>
  `)
        .join("");
    root
        .querySelectorAll('input[type="color"], input[type="text"]')
        .forEach((el) => {
        el.addEventListener("input", handleColorInput);
    });
    root
        .querySelectorAll('input[type="range"][data-alpha-key]')
        .forEach((el) => el.addEventListener("input", handleAlphaInput));
    root.querySelectorAll("button[data-locate]").forEach((btn) => {
        btn.addEventListener("click", () => {
            const key = btn.dataset.locate;
            vscode.postMessage({
                type: "LOCATE",
                payload: { elementId: demoIdForKey(key) },
            });
        });
    });
}
function handleColorInput(ev) {
    const t = ev.target;
    const key = t.dataset.key;
    let val = t.value.trim();
    const parent = t.parentElement;
    const textInput = parent.querySelector('input[type="text"][data-key="' + key + '"]');
    const colorInput = parent.querySelector('input[type="color"][data-key="' + key + '"]');
    const alphaInput = parent.querySelector('input[type="range"][data-alpha-key="' + key + '"]');
    if (t.type === "color") {
        // preserve existing alpha from text input if present
        const base6 = val; // #RRGGBB
        const aPct = alphaFromHex(textInput?.value || "");
        const merged = mergeHexWithAlpha(base6, aPct);
        textInput.value = merged;
        alphaInput.value = String(aPct);
        val = merged;
    }
    else if (t.type === "text") {
        // sync color and alpha controls from text
        if (isValidHex(val)) {
            colorInput.value = coerceHex(val);
            alphaInput.value = String(alphaFromHex(val));
            textInput.classList.remove('invalid');
            textInput.setAttribute('aria-invalid', 'false');
        }
    }
    // Guard against invalid hex; do not apply until valid
    if (!isValidHex(val)) {
        textInput.classList.add('invalid');
        textInput.setAttribute('aria-invalid', 'true');
        return;
    }
    textInput.classList.remove('invalid');
    textInput.setAttribute('aria-invalid', 'false');
    recordHistory();
    model.colors[key] = normalizeHex(val);
    pushPreview();
    // auto-locate in preview when editing a color
    vscode.postMessage({
        type: "LOCATE",
        payload: { elementId: demoIdForKey(key) },
    });
}
function handleAlphaInput(ev) {
    const t = ev.target; // range
    const key = t.dataset.alphaKey;
    const parent = t.parentElement;
    const colorInput = parent.querySelector('input[type="color"][data-key="' + key + '"]');
    const textInput = parent.querySelector('input[type="text"][data-key="' + key + '"]');
    const aPct = clampPct(parseInt(t.value, 10));
    const merged = mergeHexWithAlpha(colorInput.value, aPct);
    textInput.value = merged;
    recordHistory();
    model.colors[key] = merged;
    pushPreview();
    // auto-locate in preview when changing alpha
    vscode.postMessage({
        type: "LOCATE",
        payload: { elementId: demoIdForKey(key) },
    });
}
function clampPct(n) {
    return isFinite(n) ? Math.min(100, Math.max(0, n)) : 100;
}
function alphaFromHex(v) {
    const m = /^#[0-9a-f]{6}([0-9a-f]{2})$/i.exec(v);
    if (!m)
        return 100;
    const aa = parseInt(m[1], 16);
    return Math.round((aa / 255) * 100);
}
function mergeHexWithAlpha(base6, alphaPct) {
    const hex6 = coerceHex(base6).replace("#", "");
    const a = clampPct(alphaPct);
    const aa = Math.round((a / 100) * 255)
        .toString(16)
        .padStart(2, "0");
    return `#${hex6}${aa}`;
}
function renderTokens() {
    const root = document.getElementById("panel-tokens");
    const rows = model.tokenColors.map((r, idx) => tokenRow(r, idx)).join("");
    root.innerHTML = `
		<button id="add-token" aria-label="Add token rule">Add Rule</button>
    <div>${rows}</div>`;
    document.getElementById("add-token").addEventListener("click", () => {
        recordHistory();
        model.tokenColors.push({ scope: "", settings: {} });
        renderTokens();
        pushPreview();
    });
    root.querySelectorAll("[data-token-edit]").forEach((el) => el.addEventListener("input", (e) => {
        const t = e.target;
        const idx = Number(t.dataset.index);
        const field = t.dataset.field;
        recordHistory();
        if (field === "scope")
            model.tokenColors[idx].scope = t.value;
        if (field === "fg") {
            const v = t.value.trim();
            if (!isValidHex(v)) {
                t.classList.add('invalid');
                t.setAttribute('aria-invalid', 'true');
                return;
            }
            t.classList.remove('invalid');
            t.setAttribute('aria-invalid', 'false');
            model.tokenColors[idx].settings.foreground = normalizeHex(v);
        }
        if (field === "fs")
            model.tokenColors[idx].settings.fontStyle = t.value;
        pushPreview();
        vscode.postMessage({
            type: "LOCATE",
            payload: { elementId: "demo-editor" },
        });
    }));
    root.querySelectorAll("[data-token-remove]").forEach((btn) => btn.addEventListener("click", (e) => {
        const i = Number(e.currentTarget.dataset.index);
        recordHistory();
        model.tokenColors.splice(i, 1);
        renderTokens();
        pushPreview();
        vscode.postMessage({
            type: "LOCATE",
            payload: { elementId: "demo-editor" },
        });
    }));
    root.querySelectorAll("[data-fs]").forEach((cb) => cb.addEventListener("change", (e) => {
        const t = e.target;
        const i = Number(t.dataset.index);
        const val = t.value;
        const cur = (model.tokenColors[i].settings.fontStyle || "")
            .split(/\s+/)
            .filter(Boolean);
        const set = new Set(cur);
        recordHistory();
        if (t.checked)
            set.add(val);
        else
            set.delete(val);
        model.tokenColors[i].settings.fontStyle =
            Array.from(set).join(" ") || undefined;
        pushPreview();
        vscode.postMessage({
            type: "LOCATE",
            payload: { elementId: "demo-editor" },
        });
    }));
}
function tokenRow(r, idx) {
    const fs = (r.settings.fontStyle || "").split(/\s+/).filter(Boolean);
    const has = (k) => fs.includes(k);
    return `<div class="row">
		<label>scope</label><input data-token-edit aria-label="Token scope" data-index="${idx}" data-field="scope" value="${Array.isArray(r.scope) ? r.scope.join(", ") : r.scope || ""}" />
    <label>foreground</label><input data-token-edit aria-label="Token foreground color" data-index="${idx}" data-field="fg" value="${r.settings.foreground || ""}" />
		<fieldset style="display:inline-flex;gap:6px;border:none;padding:0;margin:0">
			<label><input type="checkbox" data-fs data-index="${idx}" value="bold" ${has("bold") ? "checked" : ""}/> bold</label>
			<label><input type="checkbox" data-fs data-index="${idx}" value="italic" ${has("italic") ? "checked" : ""}/> italic</label>
			<label><input type="checkbox" data-fs data-index="${idx}" value="underline" ${has("underline") ? "checked" : ""}/> underline</label>
			<label><input type="checkbox" data-fs data-index="${idx}" value="strikethrough" ${has("strikethrough") ? "checked" : ""}/> strikethrough</label>
		</fieldset>
		<button data-token-remove data-index="${idx}" aria-label="Remove token rule ${idx + 1}">Remove</button>
  </div>`;
}
function renderSemantic() {
    const root = document.getElementById("panel-semantic");
    const entries = Object.entries(model.semanticTokens);
    root.innerHTML = `
		<button id="add-sem" aria-label="Add semantic rule">Add Semantic</button>
    <div>${entries.map(([sel, s], i) => semRow(sel, s, i)).join("")}</div>`;
    document.getElementById("add-sem").addEventListener("click", () => {
        recordHistory();
        model.semanticTokens["entity.name.new"] = {};
        renderSemantic();
        pushPreview();
    });
    root.querySelectorAll("[data-sem]").forEach((el) => el.addEventListener("input", (e) => {
        const t = e.target;
        const i = Number(t.dataset.index);
        const entries = Object.keys(model.semanticTokens);
        const sel = entries[i];
        recordHistory();
        if (t.dataset.field === "selector") {
            const val = t.value;
            const cur = model.semanticTokens[sel];
            delete model.semanticTokens[sel];
            model.semanticTokens[val] = cur;
        }
        else if (t.dataset.field === "fg") {
            const v = t.value.trim();
            if (!isValidHex(v)) {
                t.classList.add('invalid');
                t.setAttribute('aria-invalid', 'true');
                return;
            }
            t.classList.remove('invalid');
            t.setAttribute('aria-invalid', 'false');
            model.semanticTokens[sel].foreground = normalizeHex(v);
        }
        else if (t.dataset.field === "fs") {
            model.semanticTokens[sel].fontStyle = t.value;
        }
        pushPreview();
    }));
    root.querySelectorAll("[data-sem-remove]").forEach((btn) => btn.addEventListener("click", (e) => {
        const i = Number(e.currentTarget.dataset.index);
        const entries = Object.keys(model.semanticTokens);
        const sel = entries[i];
        recordHistory();
        delete model.semanticTokens[sel];
        renderSemantic();
        pushPreview();
    }));
    root.querySelectorAll("[data-sem-fs]").forEach((cb) => cb.addEventListener("change", (e) => {
        const t = e.target;
        const i = Number(t.dataset.index);
        const entries = Object.keys(model.semanticTokens);
        const sel = entries[i];
        const cur = (model.semanticTokens[sel].fontStyle || "")
            .split(/\s+/)
            .filter(Boolean);
        const set = new Set(cur);
        recordHistory();
        if (t.checked)
            set.add(t.value);
        else
            set.delete(t.value);
        model.semanticTokens[sel].fontStyle =
            Array.from(set).join(" ") || undefined;
        pushPreview();
    }));
}
function semRow(sel, s, i) {
    const fs = (s.fontStyle || "").split(/\s+/).filter(Boolean);
    const has = (k) => fs.includes(k);
    return `<div class="row">
	<label>selector</label><input data-sem aria-label="Semantic selector" data-index="${i}" data-field="selector" value="${sel}"/>
	<label>foreground</label><input data-sem aria-label="Semantic foreground color" data-index="${i}" data-field="fg" value="${s.foreground || ""}"/>
		<fieldset style="display:inline-flex;gap:6px;border:none;padding:0;margin:0">
			<label><input type="checkbox" data-sem-fs data-index="${i}" value="bold" ${has("bold") ? "checked" : ""}/> bold</label>
			<label><input type="checkbox" data-sem-fs data-index="${i}" value="italic" ${has("italic") ? "checked" : ""}/> italic</label>
			<label><input type="checkbox" data-sem-fs data-index="${i}" value="underline" ${has("underline") ? "checked" : ""}/> underline</label>
		</fieldset>
		<button data-sem-remove data-index="${i}" aria-label="Remove semantic rule ${i + 1}">Remove</button>
  </div>`;
}
function renderPreviewDemos() {
    const root = document.getElementById("preview");
    root.innerHTML = getDemosHtml();
    // clicking preview tabs
    document.querySelectorAll(".ptab").forEach((b) => b.addEventListener("click", () => {
        document
            .querySelectorAll(".ptab")
            .forEach((x) => x.classList.remove("active"));
        b.classList.add("active");
        const id = b.dataset.demo;
        document
            .querySelectorAll(".demo")
            .forEach((x) => (x.style.display = "none"));
        document.getElementById("demo-" + id).style.display = "block";
        saveState();
    }));
}
function pushPreview() {
    if (previewTimer)
        window.clearTimeout(previewTimer);
    previewTimer = window.setTimeout(() => {
        const snap = JSON.stringify(model);
        if (snap !== lastPreviewSnapshot) {
            lastPreviewSnapshot = snap;
            vscode.postMessage({ type: "APPLY_PREVIEW", payload: model });
            try {
                vscode.setState && vscode.setState({ model, ui: collectUI() });
            }
            catch { }
        }
    }, PREVIEW_DEBOUNCE_MS);
}
function collectUI() {
    const leftBtn = document.querySelector('.tab.active');
    const left = leftBtn?.dataset.tab || 'colors';
    const ptab = document.querySelector('.ptab.active');
    const previewTab = ptab?.dataset.demo || 'editor';
    const search = document.getElementById('search')?.value || '';
    return { leftTab: left, previewTab, search };
}
function saveState() {
    try {
        vscode.setState && vscode.setState({ model, ui: collectUI() });
    }
    catch { }
}
// Map color key to demo element id
function demoIdForKey(key) {
    if (key.startsWith("statusBar"))
        return "demo-statusbar";
    if (key.startsWith("panel") || key.startsWith("panelSection"))
        return "demo-panels";
    if (key.startsWith("terminal"))
        return "demo-terminal";
    if (key.startsWith("problems"))
        return "demo-problems";
    if (key.startsWith("tab.") || key.startsWith("editorGroupHeader"))
        return "demo-lists";
    return "demo-editor";
}
// Minimal demos
function getDemosHtml() {
    return `
  <section class="demo" id="demo-editor" style="display:block">
    <div id="editor" contenteditable="true" style="min-height:220px;padding:8px;border:1px solid var(--vscode-editorWidget-border)">
      // active line, selections, cursor, hover, indent guides, etc.
      function demo() { console.log('Hello theme'); }
    </div>
  </section>
  <section class="demo" id="demo-panels" style="display:none"><div>Panels</div></section>
  <section class="demo" id="demo-problems" style="display:none"><div>Problems list</div></section>
  <section class="demo" id="demo-terminal" style="display:none"><div>Terminal</div></section>
  <section class="demo" id="demo-notifications" style="display:none"><div>Notifications</div></section>
  <section class="demo" id="demo-statusbar" style="display:none"><div>Status Bar</div></section>
  <section class="demo" id="demo-lists" style="display:none"><div>Lists/Tabs</div></section>`;
}
// Merge helpers: base retains precedence; add contributes missing keys/rules
function mergeColors(base, add) {
    return { ...add, ...base }; // base wins
}
function normScopeKey(scope) {
    if (Array.isArray(scope))
        return scope.map((s) => s.trim()).join(",");
    return String(scope || "").trim();
}
function mergeTokenRules(base, add) {
    const map = new Map();
    // base first so it wins
    for (const r of base)
        map.set(normScopeKey(r.scope), r);
    for (const r of add) {
        const k = normScopeKey(r.scope);
        if (!map.has(k))
            map.set(k, r);
    }
    return Array.from(map.values());
}
function mergeSemanticRules(base, add) {
    return { ...add, ...base }; // base wins
}
// boot
document.addEventListener("DOMContentLoaded", () => {
    // Toolbar wiring
    const byId = (id) => document.getElementById(id);
    byId("btn-import")?.addEventListener("click", () => vscode.postMessage({ type: "REQUEST_IMPORT" }));
    byId("btn-use")?.addEventListener("click", () => vscode.postMessage({ type: "REQUEST_USE_CURRENT" }));
    byId("btn-blank")?.addEventListener("click", () => vscode.postMessage({ type: "REQUEST_START_BLANK" }));
    byId("btn-undo")?.addEventListener("click", () => {
        undo();
    });
    byId("btn-redo")?.addEventListener("click", () => {
        redo();
    });
    byId("btn-export-json")?.addEventListener("click", () => vscode.postMessage({ type: "REQUEST_EXPORT_JSON" }));
    byId("btn-export-css")?.addEventListener("click", () => vscode.postMessage({ type: "REQUEST_EXPORT_CSS" }));
    byId("btn-export-vsix")?.addEventListener("click", () => vscode.postMessage({ type: "REQUEST_EXPORT_VSIX" }));
    document.querySelectorAll(".tab").forEach((t) => t.addEventListener("click", () => {
        document
            .querySelectorAll(".tab")
            .forEach((x) => x.classList.remove("active"));
        t.classList.add("active");
        const id = t.dataset.tab;
        document
            .querySelectorAll(".panel")
            .forEach((x) => x.classList.remove("active"));
        document.getElementById("panel-" + id).classList.add("active");
        saveState();
    }));
    const search = document.getElementById("search");
    search.addEventListener("input", () => {
        const q = search.value.toLowerCase();
        document.querySelectorAll("#panel-colors section").forEach((section) => {
            let any = false;
            section.querySelectorAll(".row").forEach((row) => {
                const txt = row.textContent?.toLowerCase() || "";
                const show = txt.includes(q);
                row.style.display = show ? "" : "none";
                if (show)
                    any = true;
            });
            section.style.display = any ? "" : "none";
        });
        // tokens filter
        document.querySelectorAll("#panel-tokens .row").forEach((row) => {
            const txt = row.textContent?.toLowerCase() || "";
            row.style.display = txt.includes(q) ? "" : "none";
        });
        // semantic filter
        document.querySelectorAll("#panel-semantic .row").forEach((row) => {
            const txt = row.textContent?.toLowerCase() || "";
            row.style.display = txt.includes(q) ? "" : "none";
        });
        saveState();
    });
    // Keyboard shortcuts: Undo/Redo
    window.addEventListener("keydown", (e) => {
        const ctrlOrMeta = e.ctrlKey || e.metaKey;
        if (!ctrlOrMeta)
            return;
        const key = e.key.toLowerCase();
        if (key === "z") {
            e.preventDefault();
            if (e.shiftKey)
                redo();
            else
                undo();
        }
        else if (key === "y") {
            e.preventDefault();
            redo();
        }
    });
    updateUndoRedoButtons();
    vscode.postMessage({ type: "REQUEST_BOOT" });
});
